.ba-section--english {
    padding-top:38px;
    @include breakpoint(large){
        margin-top: 50px;
    }
}
.ba-soon-cwork {
    position: relative;
    background: $black;
    color:$white;
    padding:10px 21px 0 0 ;
    z-index: 1;
    width: 100%;
    &:after {
        position: absolute;
        content:'';
        top: 0;
        right: 0;
        bottom: 0;
        width: 9999px;
        background:$black;
        z-index: 0;
    }
    &__title {
        position: relative;
        z-index: 2;
        max-width: 55px;
    }
   
}
.ba-english-classes {
    padding:15px 23px 41px 0;
    &__title {
        font-weight: bold;
        margin-bottom: 0;
        
    }
    &__date {
        display: flex;
        flex-direction: column;
       line-height: 1;
        // align-items: center;
        margin-bottom: 17px;
        text-align: center;
        & span {
            text-decoration: underline;
            font-weight: bold;
            font-size: 16px;
        }
    }
    & p {
        max-width: 189px;
        font-size: 14px;
    }
}