/// cswplace info 
.cwork-place-info {
    padding:30px 39px 0 0 ;
    @include breakpoint(medium){
        padding-top: 20px;
    }
    &__title {
        font-weight: bold;
    }
    &__text {
        font-size: 12px;
    }
    &__duration {
        text-align: center;
        font-size: 14px;
    }
    &__price {
        font-size: 18px;
        font-weight: normal;
        text-align: center;
        & span {
            font-size: 12px;
        }
    }
    &__img {
        margin-top: 20px;
        margin-bottom: 10px;
        overflow: hidden;
        @include breakpoint(large){
            margin-bottom: 0;
        }
        & img {
            display: block;
            width: 100%;
            height: 100%;
        }
    }
    &__img--top {
        max-height: 280px;
        margin-bottom: 38px;
    }
    &__img--left {
        height: 100%;
        
    }
}
.cwork-place-info-wrap {
    display: flex;
}
.ba-image-left {
    overflow: hidden;
}