body {
	font-size: 14px;
	font-weight: 400;
}
svg{
	display: inline-block;
	vertical-align: middle;
	@include ba-transition('fill, stroke');
}
a,
[type="submit"],
[type="button"],
button{
	@include ba-transition;
	display:inline-block;
}
.ba-button {
	background: #fff;
	color: $black;
	&--connect {
		line-height: 50px;
		text-transform: uppercase;
		padding:0 27px;
		margin-bottom: 51px;
		position: relative;
		z-index: 1;
	}
	&--subscribe {
		font-size: 16px;
		line-height: 50px;
		text-transform: uppercase;
		padding:0 24px;
		background:$dark-gray;
		color:$white;
		margin-left: 10px;
		margin-top: 10px;
		&:hover {
			
		}
	}
}
