.ba-footer-join {
    background:$black;
    padding:57px 42px 16px 42px;
    text-align: center;
    position: relative;
    z-index: 2;
    width: 100%;
    @include breakpoint(large){
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }
    &:after {
        background: $black;
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        content:'';
        width: 9999px;
        z-index: 0;
    }
    @include breakpoint(medium){
        padding:46px 32px 42px 6px;
    }
    &__title {
        color: $white;
        text-transform: uppercase;
        font-size: 24px;
        font-weight: bold;
        max-width: 136px;
        margin: 0;
        position: relative;
        z-index: 2;
        font-weight: normal;
        line-height: 2;
        @include breakpoint(large){
           
        }
        @include breakpoint(medium){
            text-align: right;
        }
    }
    
}
.ba-title-wrap {
    display: flex;
    justify-content: center;      
    margin-bottom: 52px;
   
   @include breakpoint(medium){
       justify-content: flex-start;
       margin-bottom: 29px;
   }
  
}
.ba-socials {
    margin: 0 auto;
    font-size: 31px;
    display: flex;
    justify-content: space-between;
    width: 112px;
    position: relative;
    z-index: 1;
    color:$white;
    @include breakpoint(medium){
        flex-direction: column;
        margin-left: 3px;
        align-items: space-between;
        font-size: 24px;
    }
    @include breakpoint(large){
        align-items: flex-end;
        width: 100%;
    }
}
.ba-footer-logo {
    position: relative;
    z-index: 2;
    margin-bottom: 20px;
}
.ba-footer-contacts {
    margin-top: 29px;
    margin-bottom: 36px;
    color:$white;
    position: relative;
    z-index: 3;
    max-width:140px;
    margin: 0 auto;
    @include breakpoint(large){
       margin: 0 0 0 auto;

    }
    & a {
        color:$white;
    }
}
.ba-map{
    height: 300px;
    width: 100%;
    @include breakpoint(medium){
        height: 100%;
    }
    @include breakpoint(large){
      
        width: 100vw;
       bottom: 0;
        left: -25%;
    }
    @include breakpoint(xxlarge){
        left: -50%;
    }
}
