.ba-cwork-place {
    position: relative;
    background: $black;
    color:$white;
    padding:10px 21px 0 0 ;
    z-index: 1;
    width: 100%;
    &:after {
        position: absolute;
        content:'';
        top: 0;
        right: 0;
        bottom: 0;
        width: 9999px;
        background:$black;
        z-index: 0;
    }
    &__title {
        position: relative;
        z-index: 2;
        max-width: 55px;
        margin-bottom: 65px;
        font-weight: bold;
        &--events {
            margin-bottom: 0;
        }
    }
    & p {
        position: relative;
        color: $white;
        z-index: 2;
    }
    &__list {
        position: relative;
        z-index: 2;
        margin: 0;
        list-style: none;
        font-size: 16px;
        & li {
            font-size: 12px;
            padding: 0;
        }
    }
  
   
}

.ba-cwork-place-list-title {
    font-weight: 400;
    position: relative;
    z-index: 2;
    margin-top: 39px;
    @include breakpoint(medium){
        font-size: 16px;
    }
}
.gray-button { 
    position: absolute;
    right: 60px;
    height: 100%;
    z-index: 5;
    width: 30vw;
    background:rgba(#000, 0.4);
    transform: translateX(100%);
    @include breakpoint(large down){
        display:none;
    }
}