.ba-slider {
	padding:32px 15px 17px;
	&:before {
		content:'';
		position: absolute;
		top: 32px;
		bottom: 17px;
		left: 0;
		width: 39px;
		background: rgba(#000,0.4);
		z-index: 2;
	}
	&:after {
		content:'';
		position: absolute;
		top: 32px;
		bottom: 17px;
		right: 0;
		width: 39px;
		background: rgba(#000,0.4);
		z-index: 2;
	}
	&__item {
		height:220px;
		background-size: cover;
		background-position:  50%;
	}
}