.ba-row-relative {
    position: relative;
}
.ba-intro {
    // padding:85px 0 311px;
    // background: url('../img/greenhouse.jpg') no-repeat 50% ;
    background-size: cover;
    color:$white;
  
    &__info {
        visibility:hidden;
       margin-top: 90px;
       transition: opacity .4s ease-in;
       opacity: 0;
        &.ba-open {
           opacity: 1;
            visibility: visible;
          
        }
     }
    &-wrap{
        background: url('../img/greenhouse.jpg') no-repeat 50% ;
        background-size: cover;
        position: relative;
        &.ba-open {
            transition: width .4s ease-in;
            width: 100vw;
            &:after{
                position: absolute;
                top: 0;bottom: 0;right: 0;
                left: 0;
                content:'';
                background: rgba(#000,0.5);
            }
           
        }

    }
    &-title {
        position: relative;
        z-index: 3;
    }
    & p {
        position: relative;
        z-index: 3;
    }
    
}
.ba-join-form {
    display: flex;
    
}
.ba-button--connect {
    display:inline-block;
    line-height: 44px;
    background: #ffffff;
    width: 265px;
    margin: 0;
    border:none;
    position: relative;
    z-index: 3;
    color: #000;
}
.ba-input--connect {
    display:inline-block;
    line-height: 44px;
    background: #ffffff;
    width: 265px;
    border:none;
    margin: 0;
    position: relative;
    z-index: 3;
    margin-right: 30px;
    padding-left: 20px;
}
.ba-intro-join {
    background:$black;
    padding:57px 32px 16px 32px;
    text-align: center;
    position: relative;
    z-index: 5;
    transition: transform .4s ease-in;
    &.ba-closed {
        transform: scaleX(0);
    }
    
    &:after {
        background: $black;
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        content:'';
        width: 9999px;
        z-index: 4;
    }
    @include breakpoint(medium){
        padding:46px 32px 42px 6px;
    }
    &__title {
        text-transform: uppercase;
        text-align: right;
        max-width: 200px;
        margin: 0;
        position: relative;
        z-index: 5;
        font-weight: normal;
        @include breakpoint(medium){
            text-align: left;
        }
    }
    & .krest  {
        position: absolute;
        left: 20px;
        top: 20px;
        z-index: 5;
    }
    
}
.ba-title-wrap {
    display: flex;
    justify-content: flex-end;      
    margin-bottom: 52px;
   
   @include breakpoint(medium){
       justify-content: flex-start;
       margin-bottom: 29px;
   }
  
}
.ba-socials--intro {
    margin: 0 auto;
    font-size: 31px;
    display: flex;
    justify-content: space-between;
    width: 112px;
    position: relative;
    z-index:5;
    color:$white;
    @include breakpoint(medium){
        flex-direction: column;
        margin-left: 3px;
        align-items: space-between;
        font-size: 24px;
    }
}
.intro-button { 
    position: absolute;
    right: 60px;
    height: 100%;
    z-index: 5;
    width: 30vw;
    background:#8F8F85;
    transform: translateX(100%);
    @include breakpoint(large down){
        display:none;
    }
}

.ba-main-join {
    background: #000000;
    width: 30vw;
 
    position: fixed;
    right: 0;
    top: 0;
    z-index: 5;
    padding-left: 67px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100vh;
    transition: transform .5s ease-in-out;
    transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
    & .ba-socials--fixed {
       width: 136px;
    }
    &.ba-closed {
        transform: translateX(100%);
    }
}
.krest-is-back {
    position: absolute;
    z-index: 5;
    bottom: 50px;
    left: 50px;
}
.ba-form-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(#000,0.6);
    z-index: 6;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
    transition: opacity .3s ease-in-out;
    visibility: visible;
    &.ba-closed {
visibility: hidden;        opacity: 0;
    }
}
.ba-sign-form {
    background: #ffffff;
    z-index: 6;
    max-width:100%;
    @include breakpoint(medium){
        padding:106px 131px 60px;
        max-width:525px;
    }
    & input {
        padding-left: 10px;
        width: 100%;
        line-height: 43px;
        margin-bottom: 32px;
    }
    & .ba-sign-button {
        line-height: 70px;
        background-color:#525248;
        width: 100%;
        color:  #fdfdfd;
font-size: 18px;
text-transform: uppercase;
    }

}
.ba-button-side {
    margin-top: 50px;
}
